.preloader {
  @extend %cover;

  overflow: hidden;
  background: $color-redbg;

  &__wrapper {
    position: relative;
    width: 100%;
    height: 100%;
  }
  &__bg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover;

    @include media('>tablet') {
      width: 100%;
    }
    @include media('<=tablet') {
      width: 100%!important;
      height: 100%;

      max-width: none;
      box-sizing: border-box;
      margin: 0;
      padding: 0!important;
    }
  }

  &__slider {
    @extend %cover;

    @include media('<=phone') {
      padding: 0 16px;
    }

    &-wrapper {
      position: relative;
      height: 100%;
    }
  }
  &__slide {
    @extend %cover;
    @extend %center;

    
    height: 100%;
    opacity: 0;
    

    &._active {
      opacity: 1;
      transition: opacity 3s $easy-out-expo;

      &:not(:last-child) {
        animation: animationEnd 3s $easy-out-expo forwards 2.5s;
        @keyframes animationEnd {
          to {
            opacity: 0;
          }
        }
      }
    }

    // &:nth-child(1) {
    //   opacity: 0;
    // }
    // &:nth-child(2) {
    //   opacity: 1;
    // }
    
    &:nth-child(3) {
      .preloader__slide-content ._container {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0;
        margin: 0;
        width: 100%;
      }
    }
  }
  &__calendar {
    @extend %center;

    width: 56.2rem;
    height: 56.2rem;
    @include media('>tablet', '<desktop') {
      @media(max-height: 700px) {
        width: 40.8rem;
        height: 40.8rem;
      }
      @media(max-height: 560px) {
        width: 30.8rem;
        height: 30.8rem;
      }
    }
    
    @include media('<=phone') {
      width: 100%;
    }
    @include media('<=tablet') {
      width: 71.4rem;
      height: 71.4rem;
  
      @media(max-height: 850px) {
        width: 51.4rem;
        height: 51.4rem;
      }
      @media(max-height: 650px) {
        width: 31.4rem;
        height: 31.4rem;
      }
    }
    @include media('>=desktop') {
      width: 86.8rem;
      height: 86.8rem;
      @media(max-height: 1040px) {
        width: 76.8rem;
        height: 76.8rem;
      }
    }
   

    img {
      max-width: 100%;
      max-height: 100%;
    }
  }

  &__title {
    @extend %ricordi-62;

    max-width: 92rem;
    text-align: center;
    color: $color-white;
    text-transform: uppercase;
    display: block;
    margin-bottom: 7.2rem;

    overflow: hidden;
    div {
      overflow: hidden;
    }

    @include media('>=desktop') {
      max-width: 140rem;
    }
  }
  &__button {
    @extend %ricordi-16;
    @extend %center;

    width: 20.5rem;
    height: 8rem;
    text-align: center;
    color: $color-white;
    text-transform: uppercase;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 6.1rem;
    cursor: pointer;
    position: relative;
    overflow: hidden;

    span {
      position: relative;
    }

    @include media('<=phone') {
      width: 100%;
    }

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: url('../../images/button.jpeg'), linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('../../images/button.jpeg'), #FFFFFF;
      background-size: cover;
      background-position: 70% 90%;
      filter: brightness(0.4);
    }
  }
}

.timer {
  width: 62.6rem;

  @include media('>=desktop') {
    width: 72.1rem;
  }

  @include media('<=phone') {
    width: 14.3rem;
  }

  &__wrapper {
    display: flex;
    width: 100%;

    @include media('<=phone') {
      flex-direction: column;
    }
  }
  &__item {
    width: 100%;

    @include media('<=phone') {
      margin-bottom: 3.2rem;
    }

    &:first-child {

      .timer__item-title {
        border-left: 1px solid rgba(#fff, 0.3);

        @include media('<=phone') {
          border-right: 1px solid rgba(#fff, 0.3);
        }
      }
      .timer__item-date {
        &:after {
          @extend %ricordi-124;

          content: ':';
          color: $color-white;
          position: absolute;
          right: -8%;
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
    &:nth-child(2) {

      .timer__item-title {
        border-left: 1px solid rgba(#fff, 0.3);
        border-right: 1px solid rgba(#fff, 0.3);
      }
      .timer__item-date {
        &:after {
          @extend %ricordi-124;

          content: ':';
          color: $color-white;
          position: absolute;
          right: -8%;
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
    &:last-child {
      .timer__item-title {
        border-right: 1px solid rgba(#fff, 0.3);

        @include media('<=phone') {
          border-left: 1px solid rgba(#fff, 0.3);
        }
      }
    }
    &-title {
      @extend %center;
      @extend %manrope-12;

      height: 3.3rem;
      border-bottom: 1px solid rgba(#fff, 0.3);
      color: $color-white;
      margin-bottom: 1.8rem;

      @include media('<=phone') {
        margin-bottom: 0;
      }

      span {
        margin-bottom: 1.2rem;
      }
    }
    &-date {
      @extend %center;
      @extend %ricordi-124;

      position: relative;
      color: $color-white;

      span {
        overflow: hidden;
      }

      @include media('<=phone') {
        &:after {
          display: none;
        }
      }
    }
  }
}
